
@color-blue: #1652F0;
@color-red: #ff7f00;
@color-green: #4fbb92;

body {
    background: #1e2230 url("../../images/download/bg.jpg") 0 0 no-repeat;
    background-size: 100%;
    color: #fff;
}

.module-download {
    padding: 32px 0;
    .download-header {
        margin-bottom: 10px;
        text-align: center;
        .header-logo {
            margin-bottom: 10px;
        }
        .header-title {
            font-weight: bold;
            font-size: 24px;
            line-height: 1;
        }
        .header-www {
            opacity: 0.85;
        }
    }
    .download-body {
        margin-bottom: 20px;
        text-align: center;
        .body-text {
            padding: 0 1rem;
            margin-bottom: 10px;
            .body-p1 {
                font-style: italic;
                font-size: 18px;

            }
            .body-p2 {
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 10px 0;
                .p2-left, .p2-right {
                    height: 30px;
                    border-right: 5px solid #fff;
                }
                .p2-left {
                    margin-right: 4px;
                }
                .p2-right {
                    margin-left: 4px;
                }
                .p2-text {
                    line-height: 1;
                    vertical-align: middle;
                    font-size: 32px;
                    letter-spacing: 3px;
                    font-weight: bold;
                }
            }
            .body-p3 {
                opacity: 0.85;
                .p3-text {
                    letter-spacing: 4px;
                }
            }
        }
        .body-ap {

        }
    }
    .download-footer {
        .download {
            display: flex;
            justify-content: center;
            padding: 0 10px;
        }
        .download-btn {
            &:first-child {
                margin-right: 16px;
            }
            .download-link {
                display: block;
                img {
                    height: 52px;
                }
            }
        }
        .footer-logo {
            margin-top: 20px;
        }
    }
}
